
export default {
	
	onLaunch: function() {
		uni.getStorage({
			key: 'userInfo',
			success(res){
				uni.switchTab({
					url: 'pages/index/index'
				});
			},
			fail() {
				uni.redirectTo({
					url: 'pages/loginPage/loginPage'
				});
				uni.showModal({
					content: '请登录',
					showCancel: false
				});
			}
		})
		
	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
	}
}
